@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
  width: 100vw;
  height: 100vh;
  padding-top: 100px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  background-color: #cccccc;
}

.container{width:400px;margin:0 auto;margin-top:20px;padding:40px;display:flex;flex-direction:column;align-items:center;background-color:#000;font-family:"Roboto",sans-serif}.container__text{margin-bottom:40px;align-self:flex-end;font-size:12px;font-weight:300;letter-spacing:1px;color:#9df}.container__footer{color:#ccc;font-size:12px;font-weight:300}.icon{width:40px;height:40px;display:flex;justify-content:center;align-items:center;background-color:#ff80bf;border-radius:50%}.icon__img{width:20px;height:20px}.form{width:100%;display:flex;flex-direction:column;margin-top:20px;color:#fff}.form__title{font-size:20px;font-weight:500;text-align:center}.form__name{display:flex;width:100%;justify-content:space-between}.form__name input{width:42%}.form input{padding:10px;color:#fff;background-color:#000;border:0.1px solid #ccc;margin-bottom:15px}.form__checkbox{display:flex;justify-content:flex-start;align-items:flex-start}.form__inp{width:30px;height:30px;align-self:flex-end}.form__label{margin-left:15px;text-align:left;font-size:14px;color:#fff}.form__btn{width:100%;padding:10px 0;margin-bottom:30px;background-color:#9df;border:1px solid #9df;border-radius:5px;font-size:12px;font-weight:600;text-transform:uppercase;letter-spacing:1px;color:#000}.form__btn:hover{background-color:#0099e6;border:1px solid #0099e6}

.container__textarea{margin-bottom:40px;width:100%;display:flex;justify-content:space-between;font-size:12px;font-weight:300;letter-spacing:1px;color:#9df}.container__link{color:#9df}.form__short{padding-bottom:8px}

